import React from 'react';
import PropTypes from 'prop-types';
import { ResponsiveBar } from '@nivo/bar';
import language from '../../../../constants/language';

const PRViz = ({ data, selectedLanguage }) => {

  const colorBy = d => '#EB5941';
  return (
    <ResponsiveBar
      data={data}
      layers={['grid', 'axes', 'bars', 'markers', 'legends', 'annotations', 'Titulo']}
      keys={['pr']}
      indexBy="username"
      margin={{ top: 50, right: 90, bottom: 50, left: 90 }}
      padding={0.5}
      enableGridX={true}
      enableGridY={false}
      colorBy={colorBy}
      layout="horizontal"
      enableLabel={false}
      axisBottom={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: language[selectedLanguage].pullRequest.labelX,
        legendPosition: 'middle',
        legendOffset: 32
      }}
      axisLeft={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0
      }}
    />
  );
};

PRViz.propTypes = {
  data: PropTypes.array.isRequired,
};

export default PRViz;
