class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      pr_users: [],
    };
  }

  componentDidMount() {
    this.props.changeTrazabilityHours({
      selectedRepo: this.props.selectedRepo,
      startDate: this.props.startDate,
      endDate: this.props.endDate,
      user: this.props.userId,
    });
    this.props.fetchGetLinkedRepos(this.props.projectId).then((response) => {
      if (response.ok) {
        this.setState({ repos: response.body });
        if (response.body) {
          this.getPRbyUser();
        }
      }
    });
  }

  getPRbyUser() {
    const { repos } = this.state;
    const body = { 'repo_id': repos[0].id };
    const pr_users = [];
    this.props.fetchGetPR(this.props.projectId, body).then((response) => {
      console.log(response);
      if (response.ok) {
        response.body.get_pull_requests.forEach(element => {
          pr_users.push({ 'username': element.login, 'pr': element.pr, 'color': "#EB5941" })
        })
        this.setState({ loading: false, pr_users });
      }
    });
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const { classes, selectedLanguage } = this.props;
    const { pr_users } = this.state;
    return (
      <div className={classes.container} >
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].report.pullrequest}
          </Typography>
          <div className={classes.line} />
        </div>
        <div style={styles.graph} className={classes.wrapper}>
          {pr_users.length > 0 && <PRViz data={pr_users} selectedLanguage={selectedLanguage} />}
        </div>
      </div >
    );
  }
}

UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  users: PropTypes.array.isRequired,
  userId: PropTypes.string.isRequired,
  fetchVizHoursPerTask: PropTypes.func.isRequired,
  fetchVizGithub: PropTypes.func.isRequired,
  fetchGetLinkedRepos: PropTypes.func.isRequired,
  fetchGetPR: PropTypes.func.isRequired,
  fetchGetCommitsPerPR: PropTypes.func.isRequired,
  fetchGetPRLeadTime: PropTypes.func.isRequired,
  changeTrazabilityHours: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  selectedRepo: PropTypes.any.isRequired,
  user: PropTypes.any.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    userId: state.auth.id,
    selectedRepo: state.project.trazabilityhoursRepository,
    user: state.project.trazabilityhoursUser,
    endDate: state.project.trazabilityhoursEndDate,
    startDate: state.project.trazabilityhoursStarDate,
    users: allMembers(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerTask,
      fetchGetPR,
      fetchGetPRLeadTime,
      fetchGetCommitsPerPR,
      fetchVizGithub,
      fetchGetLinkedRepos,
      changeTrazabilityHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
