import {
  FETCH_VIZ_HOURS_PER_BOARD,
  FETCH_VIZ_HOURS_PER_LABEL,
  FETCH_VIZ_HOURS_PER_USER,
  FETCH_VIZ_HOURS_PER_TASK,
  FETCH_VIZ_HOURS_TIMELINE,
  FETCH_VIZ_COMMITS_PER_PR,
  FETCH_VIZ_LINKED_REPOS,
  FETCH_VIZ_PR_LEAD_TIME,
  FETCH_VIZ_PR,
  FETCH_VIZ_GITHUB,
} from '../../constants/api/viz';

export const fetchVizHoursPerBoard = (id, body) => ({
  type: FETCH_VIZ_HOURS_PER_BOARD,
  fetch: true,
  url: `/viz/projects/${id}/hours_per_board`,
  params: {
    method: 'GET',
  },
  queryParams: {
    ...body,
  },
});

