import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

const getRef = obj => (obj != null ? obj.ref : null);

// Return all the users of a project
export const allMembers = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.user.selectedProjectId,
  (session, projectId) => {
    const projectMembers = getRef(session.Project.get(o => o.id === projectId));
    if (projectMembers === null) return [];
    const members = session.Member
      .filter(o => projectMembers.members.includes(o.id))
      .orderBy(o => o.name.toUpperCase())
      .toRefArray();
    return members;
  },
);
/*
Returns all the users of a project. Each user comes with an attribute
assigned<bool>, which is used to know if the user is asigned to the task
or not.
*/
export const onlyMembers = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.board.selectedTask,
  (props, state) => state.user.selectedProjectId,
  (session, taskId, projectId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    const taskMembers = task.assigned;
    const projectMembers = session.Project.get(o => o.id === projectId).ref.members;
    const members = session.Member
      .filter(o => projectMembers.includes(o.id))
      .orderBy(o => o.name.toUpperCase())
      .toRefArray();
    return members.map((mem) => {
      return Object.assign({}, mem, {
        assigned: taskMembers.includes(mem.id),
      });
    });
  },
);

// Returns all the users asigned to a single task.
export const oneTaskMembers = createSelector(
  orm,
  (_, state) => state.orm,
  props => props.taskId,
  (session, taskId) => {
    const task = getRef(session.Task.get(o => o.id === taskId));
    if (task === null) return null;
    return Object.assign({}, {
      assigned: session.Member.filter(o => task.assigned.includes(o.id)).toRefArray(),
    }).assigned;
  },
);

export const onlyTaskMembers = createSelector(
  orm,
  (_, state) => state.orm,
  (prop, state) => state.board.selectedTask,
  (session, taskId) => {
    const task = getRef(session.Task.get(o => o.id === taskId));
    if (task === null) return null;
    return Object.assign({}, {
      assigned: session.Member.filter(o => task.assigned.includes(o.id)).toRefArray(),
    }).assigned;
  },
);

export const evaluableMembers = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.user.selectedProjectId,
  (session, projectId) => {
    const projectMembers = getRef(session.Project.get(o => o.id === projectId));
    if (projectMembers === null) return [];
    const members = session.Member
      .filter(o => projectMembers.members.includes(o.id) && o.role !== 'viewer')
      .orderBy(o => o.name.toUpperCase())
      .toRefArray();
    return members;
  },
);