import {
  TOGGLE_ASSIGN_MODAL_REPO,
  ARCHIVE_SEARCH,
  REPORT_HOURS,
  USERS_HOURS,
  TRAZABILITY_HOURS,
  TIMELINE_HOURS,
  TOGGLE_FILTER,
} from '../constants/project';

export const toggleRepoAssignForm = id => ({
  type: TOGGLE_ASSIGN_MODAL_REPO,
  payload: {
    body: {
      id,
    },
  },
});

export const archiveSearch = body => ({
  type: ARCHIVE_SEARCH,
  payload: {
    body,
  },
});

export const changeReportHours = body => ({
  type: REPORT_HOURS,
  payload: {
    body,
  },
});

export const changeUsersHours = body => ({
  type: USERS_HOURS,
  payload: {
    body,
  },
});

export const changeTrazabilityHours = body => ({
  type: TRAZABILITY_HOURS,
  payload: {
    body,
  },
});

export const changeTimelineHours = body => ({
  type: TIMELINE_HOURS,
  payload: {
    body,
  },
});

export const toggleTaskFilter = () => {
  return {
    type: TOGGLE_FILTER,
  };
};
